@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  
}

div {
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
}

.header {
  border: solid 2px blue;
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1000;
}

.content{
  border: solid 2px green;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.header-base {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 60px;
    width: 100%;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
}

.locator {
    padding: 10px;
    font-family: 'Russo One', sans-serif;
    font-size: 2em;
}

.menu-item {
    padding: 10px;
    height: 40px;
    width: 100%;
    background-color: white;
    color: red;

}
.menu-item:hover {
    background-color: red;
    color: white;
}

.user-control {
    display: flex;
    overflow: visible;
    padding: 10px;
}

.user-face {
    border-radius: 20px;
    height: 40px;
    width: 40px;
    background-color: lightcoral;
}

.user-menu {
    overflow: hidden;
    height: 40px;
}
.user-menu:hover {
    /* height: auto; */
    overflow: visible;
}
.scrawl-item {
    position: absolute;
}

.scrawl-text-new {
    width: 200px;
}

.scrawl-submit-btn {
    background-color: blue;
}
.wall-frame {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
}
.mode-menu {
    position: absolute;
    display: flex;
    top: 0px;
    left: 0px;
    width: 150px;
    justify-content: space-between;
    z-index: 1001;
}

/* .mode-menu.elem {
    z-index: 1000;
} */

.mode-menu-item {
    border-radius: 20px;
    height: 40px;
    background-color: red;
    color: white;
    padding: 5px;
}
.mode-menu-item:hover {
    background-color: white;
    color: red;
}

.mode-menu-backdrop {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: 1000;
}
